import * as React from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { IconName } from "@fortawesome/pro-light-svg-icons"
import { Link } from "gatsby"

export type ContentSectionItem = {
  icon: IconName
  title: string
  excerpt: string
  url: string
}

export type ContentSectionProps = {
  title: string
  url: string
  synopsis: string
  contentItems: ContentSectionItem[]
}

type ContentCardProps = {
  icon: IconName
  title: string
  excerpt: string
}

const ContentCard: React.FC<ContentCardProps> = ({ icon, title, excerpt, children }): React.ReactElement => {
  return (
    <div className="border hover:ring hover:ring-gray-100 hover:ring-offset-2 hover:ring-offset-white dark:hover:ring-offset-2 dark:hover:ring dark:hover:ring-deep-navy-700 dark:hover:ring-offset-deep-navy-800 rounded-sm py-6 px-4 dark:bg-deep-navy-900 dark:border-none">
      <FontAwesomeIcon icon={["fal", icon]} size="3x" className="text-tangerine" />
      <h3 className="font-rocgrotesk font-bold my-4">{title}</h3>
      <div className="font-sans font-light">{excerpt}</div>
    </div>
  )
}

const ContentSection: React.FC<ContentSectionProps> = ({ title, url, synopsis, contentItems }): React.ReactElement => {
  return (
    <div className="text-black bg-white dark:text-white dark:bg-deep-navy-800 my-28">
      <div className="max-w-8xl mx-auto flex justify-center px-12 lg:px-6">
        <div className="flex-col justify-start">
          <Link to={url}>
            <span className="font-rocgrotesk-bold text-3xl hover:text-tangerine">{title}</span>
          </Link>
          <div className="py-4">
            <span className="font-sans font-light text-xl">{synopsis}</span>
          </div>
          <div className="py-8 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-8 content-evenly">
            {contentItems.map((item) => (
              <Link to={item.url} className="basis-1/5">
                <ContentCard icon={item.icon} title={item.title} excerpt={item.excerpt} />
              </Link>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}

export default ContentSection
