import * as React from "react"
import Helmet from "react-helmet"
import { graphql, useStaticQuery } from "gatsby"

const metadataQuery = graphql`
  query {
    site {
      siteMetadata {
        description
        title
      }
    }
  }
`

export type MetaObject = {
  name: string
  content: string
}

export type SeoProps = {
  description: string
  lang: string
  meta: MetaObject[]
  keywords: string[]
  title: string
}

const Seo = ({ description, lang, meta, keywords, title }: SeoProps): React.ReactElement => {
  const { site } = useStaticQuery(metadataQuery)
  const mergedDescription: string = description || site.siteMetadata.description

  let mergedMeta = [
    {
      name: `description`,
      content: mergedDescription,
    },
    {
      name: `og:title`,
      content: title,
    },
    {
      name: `og:description`,
      content: description,
    },
    {
      name: `og:type`,
      content: `website`,
    },
  ]

  // Merge in any keywords specified by the consumer
  mergedMeta = mergedMeta.concat(
    keywords.length > 0
      ? {
          name: `keywords`,
          content: keywords.join(`, `),
        }
      : []
  )

  // Merge in any custom metadata tags specified by the consumer
  mergedMeta = mergedMeta.concat(meta)

  return (
    <Helmet
      htmlAttributes={{
        lang,
      }}
      title={title}
      titleTemplate={`%s | ${site.siteMetadata.title}`}
      meta={mergedMeta}
    />
  )
}

Seo.defaultProps = {
  lang: `en`,
  meta: [],
  keywords: [],
  description: ``,
}

export default Seo
